@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: #2f303a;
  color: #fdfdfd;
}

.navbar {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  margin-left: 40px;
  margin-right: 40px;
}
.title {
  font-size: 40px;
  font-weight: bold;
}
.ProfileImage {
  width: 86px;
  height: 76px;
}
.navMenu {
  display: flex;
  align-items: center;
}
li {
  list-style: none;
  padding: 0 30px;
}
li a {
  text-decoration: none;
  color: #fdfdfd;
  font-size: 22px;
  line-height: 26px;
  font-weight: bold;
}
.navMenu li:nth-child(1) {
  background-color: #fdfdfd;
  padding: 5px 14px;
  color: black;
}
.navMenu li:nth-child(1) > a {
  color: black;
}
.burger {
  display: none;
}

@media screen and (max-width: 1020px) {
  .navbar {
    display: flex;
    align-items: center;
  }
  .navMenu {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 0.7;
    transition: all 0.5 ease;
    background-color: #2f303a;
  }
  .navMenu.active {
    margin-top: 30px;
    left: 0;
    opacity: 1;
    transition: all 0.5 ease;
    z-index: 1;
    background-color: rgba(47, 48, 58, 0.8);
  }
  .navListItem {
    margin-bottom: 20px;
  }
  .navListItem img {
    width: 0;
  }
  .burger {
    display: block;
    font-size: 30px;
  }
  .title {
    font-size: 30px;
  }
}

