.SideContentWrapper {
  width: 100%;
  margin: 0 auto;
  text-align: center;
  position: relative;
}
.ambulanceImage {
  width: 70%;
  margin: auto;
}
.sideData {
  font-size: 18px;
  line-height: 23px;
  margin-left: 40px;
  font-weight: normal;
  text-align: left;
  position: absolute;
  top: 80%;
  right: 0;
}

@media screen and (max-width: 640px) {
  .sideData {
    position: relative;
    font-size: 14px;
    text-align: center;
    padding: 5px;
    text-align: left;
  }
}

.PatientFormWrapper {
  background-color: #ffffff;
  margin: 50px auto;
  color: #000000;
  text-align: center;
  width: 420px;
}
.patientForm {
  display: flex;
  flex-direction: column;
  padding: 20px 40px;
  width: 100%;
  height: 558px;
}
.formTitle {
  font-size: 33px;
  line-height: 39px;
  font-weight: bold;
}
.fillDetails {
  font-size: 16px;
  line-height: 19px;
}
.patientName {
  margin-top: 27px;
}
input,
button {
  height: 37px;
  margin-top: 22px;
  border: none;
  border-bottom: 2px solid #f59494;
  background-color: #edf2f7;
  outline: none;
}
input[type="text"] {
  font-size: 16px;
  padding: 10px 15px;
}
.submitBtn {
  background-color: #f59494;
  font-size: 18px;
  line-height: 21px;
  font-weight: bold;
  color: #000;
  padding: 8px 0;
  text-align: center;
  cursor: pointer;
}
.trackPatientBtn {
  background-color: #f59494;
  font-size: 18px;
  line-height: 21px;
  font-weight: bold;
  color: #000;
  padding: 8px 0;
  text-align: center;
  cursor: pointer;
}
@media screen and (max-width: 960px) {
  .PatientFormWrapper {
    width: 90%;
    margin: auto;
    margin-top: 20px;
  }
}
@media screen and (max-width: 640) {
  .PatientFormWrapper {
    margin-top: 100px;
  }
}

.Table {
  overflow-x: auto;
}
.table {
  width: 1220px;
  margin: 40px auto;
}
table,
th,
td {
  border-collapse: collapse;
}
tr:nth-child(even) {
  background-color: #d3d9e0;
}
tr:nth-child(odd) {
  background-color: #fdfdfd;
}
th {
  background-color: #f59494;
  color: #000000;
  font-size: 20px;
  padding-top: 5px;
  padding-bottom: 2px;
  font-weight: normal;
}
td {
  text-align: center;
  color: #000000;
  font-size: 20px;
  padding-top: 5px;
  padding-bottom: 2px;
  font-weight: normal;
}
tr:nth-child(odd) > td:nth-child(even) {
  background-color: #edf2f7;
}

